@use "./portfolio";
@use "./skills";
@use "./header";
@use "./round-a-btn";
@import "./variables";

body,
html {
  min-width: 280px;
  margin: 0;
  font-family: "Quicksand", sans-serif;
}

p,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

h2 {
  padding: 1em 0;
  font-size: 2em;
  font-weight: 600;
}

header,
footer,
section {
  --padding: clamp(20px, 3vw, 100px);
  padding-left: var(--padding);
  padding-right: var(--padding);
  // border: 1px solid red;
}
